.grid_container{
position:relative;
right: 30px;
width: 300px;
display: grid;
grid-template-columns: 200px repeat(2, 1fr);
grid-template-rows: repeat(auto, 50px);
grid-column-gap: 20px;
}

.grid_item{
position: relative;
padding: 5px;
width: 150px;

border-radius: 5px;
}

.header{
position: relative;
font-family: 'Josefin Sans', bold;
font-size: 20px;
text-align: center;
}

.AddItemBtn{
position: relative;
width: 550px;
height: 40px;
font-family: 'Josefin Sans', bold;
font-size: 30px;
border: 3px solid gray;
background-color: light blue;
border-radius: 20px;
}

.listItem{
background-color: gray;
margin: 10px;
}

.selectItem{
width: 180px;
}