.grid_container{
display: grid;
font-size: 16px;
grid-template-columns: 50% 50%;
}

.inner_grid_container{
display: grid;
padding: 10px 50px;
grid-template-rows: repeat(5, minmax(10px, auto));
}

.list_container{
display: grid;
padding: 10px 50px;
grid-template-rows: repeat(auto, minmax(10px, auto));
}
