@import url('https://fonts.googleapis.com/css?family=Anton');

.primary{
background-color: #7fffd4;
}

.grid-container{
padding: 30px;
width: 100%;
height: 100px;
display: grid;
grid-template-columns: repeat(6, 1fr);
}

.logo{
width: 100px;
height: 100px;
border-radius:100px;
grid-column-start: 0;
grid-column-start: 1;
}

.print{
width: 50px;
height: 50px;
grid-column-start: 0;
grid-column-start: 1;
}

.download{
width: 60px;
height: 60px;
grid-column-start: 0;
grid-column-start: 1;
}
.brandName{
position: relative;
top: 15px;
grid-column: span 3;
font-family: 'Josefin Sans', bold;
}
.Print{
grid-column: span 1;
}
.Download{
grid-column: span 1;
}

.button{
font-size: 20px;
font-family: 'Poppins', cursive;
background-color: transparent;
position: relative;
border: none;
}